.base-learn {
    display: flex;
    flex-direction: column;

    margin: 0 20px;
    margin-bottom: 75px;
 
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    background-color: #ffffff;

}

.base-learn__image {
    margin: 0;
    padding: 0;
    height: 200px;

    border-top: transparent;
    border-radius: 10px 10px 0 0;

    
    background-image: url(../../img/IMG_8722.JPG);
    background-position: center 65%;
    background-size: cover;
    background-repeat: no-repeat;
}   

.base-learn__title {
    font-style: 20px;
    line-height: 24px;
    font-weight: 700;
    text-align: center;   

    margin: 20px 0;
    padding: 0;
    width: 100%;
}

.base-learn-list {
    list-style: none;
    margin: 0;
    padding: 0 25px;  
    padding-left: 30px;
}

.base-learn-list__item {
    margin: 10px 0;
    position: relative;
    

    &::after {
        content: '';

        position: absolute;
        bottom: 0;
        left: 0;

        width: 100%;
        height: 1px;
        background-color: #000000;
        opacity: 0.2; 
    }
}

.base-learn__text {
    font-family: 'Ubuntu',Arial,sans-serif;
    font-weight: 300;
    color: #000000;
    text-align: center;

    margin: 25px; 
}

.base-learn__price {
    font-family: 'Ubuntu',Arial,sans-serif;
    font-weight: 400;
    font-size: 20px;
    color: #000000;
    text-align: center;

    margin: 25px; 
}

.base-learn__details {
    display: block;
    box-sizing: border-box;

    width: 260px;
    height: 50px;
    margin: 50px auto;
    padding: 15px;

    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    letter-spacing: 0.5px;
    text-align: center;
    text-transform: uppercase;  

    color: #ffffff;
    background-color: #222222;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;

    cursor: pointer;
}
@keyframes bounce {
    0%   { transform: translateY(-2000px); }
    50%  { transform: translateY(50px)   ; }
    75%  { transform: translateY(-10px)  ; }
    100% { transform: translateY(0)      ; }
}

@keyframes shake {
    0%, 100% { transform: translateX(0) ;    }
    30%, 70% { transform: translateX(-20px); }
    50%      { transform: translateX(20px);  }
}

@keyframes close {
    0%   { transform: translateY(0); }
    50%  { transform: translateY(-10px)   ; }
    75%  { transform: translateY(50px)  ; }
    100% { transform: translateY(-2000px)      ; }
}


.base-learn__modal-details {
    display: block;
    position: fixed;
    width: 90vw;
    height: 90vh;
    top: 8vh; 
    left: 5vw; 
    z-index: 10;

    padding: 0;
    margin: 0;

    border-radius: 4vw;
    background-color: #ffffff;
    box-shadow: 0px 16px 20px rgba(0,1,1,0.75); 
}


.base-learn__modal-details--close {
    width: 4vw;
    height: 4vw; 
    margin-top: 3vh;
    margin-left: 80vw;

    padding: 0;
    border: 0;
    
    background-color: transparent;  
    opacity: 0.9;

}

.base-learn__modal-details__text {
    font-family: 'Ubuntu',Arial,sans-serif;
    font-weight: 300;
    color: #000000;
    text-align: center;

    margin: 0 2vw;
}

.base-learn__modal-details__image {
    margin-top: 2vh;
    padding: 0;
    height: 40vh;

    border-top: transparent;
    border-radius: 0;

    
    background-image: url(../../img/IMG_8722.JPG);
    background-position: center 65%;
    background-size: cover;  
    background-repeat: no-repeat;
} 

.base-learn__modal-details__buy { 
    display: block;
    box-sizing: border-box;

    width: 260px;
    height: 50px;
    margin: 7vh auto;
    padding: 15px;

    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    letter-spacing: 0.5px;
    text-align: center;
    text-transform: uppercase;

    color: #ffffff;
    background-color: #222222;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;

    cursor: pointer; 
}


.base-learn__modal-details--modal-close {
    
    animation-name: close;
    animation-duration: 2s;
    display: none;
    
    
}

.base-learn__modal-details--modal-show {
    display: block;
    animation-name: bounce;   
    animation-duration: 2s; 
}

.base-learn__modal-details--modal-error {
    animation-name: shake;
    animation-duration: 2s;
}

.base-learn__modal-buy {
    display: none;
    position: fixed;
    width: 90vw;
    height: 90vh;
    top: 8vh; 
    left: 5vw; 
    z-index: 11;

    padding: 0;
    margin: 0;

    border-radius: 4vw;
    box-shadow: 0px 16px 20px rgba(0,1,1,0.75); 
    background-color: #ffffff;
}
  
.base-learn__modal-buy__title {
    font-size: 25px;
    line-height: 30px;
    font-weight: 700;
    text-align: center;
    color: #000000;

    width: 80%;
    margin: 3vh auto;
      
    
}

.base-learn__modal-buy--close {
    position: absolute;
    width: 5vw;
    height: 5vw; 
    top: 3vh;
    left: 80vw;

    padding: 0;
    border: 0;
    
    background-color: transparent;  
    opacity: 0.9;
}
  
.base-learn__modal-buy__items {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      
      margin-top: 8vh;
}
  
.base-learn__modal-buy__item {
      display: flex;
      flex-direction: column;
      
      width: 75vw;
      margin: 2vh auto;
}
    
.base-learn__modal-buy label {
    font-size: 16px;
    line-height: 18px;
    font-weight: 300;
    color: #000000;
    
    margin-bottom: 7px;
}
  
.base-learn__modal-buy input {
    font: inherit;
    background-color: transparent;
  
    box-sizing: border-box;
    width: 70vw;
    border: 2px solid #000000;
    border-radius: 3px;
    padding: 12px 12px;
    height: 50px;
  
}

.base-learn__modal-buy__button-buy {
    display: block;
    box-sizing: border-box;
    border: none;

    width: 260px;
    height: 50px;
    margin: 3vh auto;
    padding: 15px;

    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    letter-spacing: 0.5px;
    text-align: center;
    text-transform: uppercase;

    color: #ffffff;
    background-color: #000000;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;

    cursor: pointer; 
}

.base-learn__modal-buy--modal-close {
    animation-name: close;
    animation-duration: 2s;
    
    
}

.base-learn__modal-buy--modal-show {
    display: block;
    animation-name: bounce;   
    animation-duration: 2s; 
}

.base-learn__modal-buy--modal-error {
    animation-name: shake;
    animation-duration: 2s;
}
